"use client";

import { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import api from "@/utils/apiAuth";
import Link from "next/link";
import { toast } from "react-hot-toast"
import { useRouter } from "next/navigation";

export default function Login() {
  const searchParams = useSearchParams()
  const emailDaUrl = searchParams.get("email") || ""

    const router = useRouter();


  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");


  useEffect(() => {
    if (emailDaUrl) setEmail(emailDaUrl)
  }, [emailDaUrl])

  const lidaComLogin = async (e: React.FormEvent) => {
    e.preventDefault();
      if (!email || !password) {
      toast.error("Preencha todos os campos.");
      return;
    }

   try {
  const res = await api.post("/login", { email, password });

  localStorage.setItem("token", res.data.token);

  toast.success(`Bem-vindo, ${res.data.user.name}!`);

  setTimeout(() => {
  router.push("/profile");
  }, 1200);
} catch (err) {
  toast.error("Erro ao fazer login.");
}
  };

  return (
    <div className="flex min-h-screen">

      <div className="hidden lg:block w-1/2 relative">
        <img
          src="/img-03.jpg"
          alt="Login background"
          className="absolute inset-0 w-full h-full object-cover"
        />
      </div>

      <div className=" bg-[#3b5353] w-full lg:w-1/2 flex items-center justify-center p-10">
        <div className="w-full max-w-md space-y-5">
          <h1 className="text-5xl font-extrabold text-white max-w-4xl mx-auto">
            Acesse sua conta e ajude a forjar um amanhã sustentável.
          </h1>

          <p className="mt-6 text-lg max-w-2xl mx-auto text-white font-extrabold">
            Produzir aço de forma sustentável é possível — e estamos construindo esse futuro agora.
          </p>

          <form onSubmit={lidaComLogin} className="flex flex-col gap-4">
            <div className="flex flex-col gap-1">

              <input
                type="email"
                placeholder="Email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="
    bg-transparent border-0 border-b border-white/30
    focus:border-white/60 focus:outline-none
    text-white placeholder-white/40
    p-2 transition-all duration-300
  "
              />
            </div>

            <div className="flex flex-col gap-1">
              <input
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="
    bg-transparent border-0 border-b border-white/30
    focus:border-white/60 focus:outline-none
    text-white placeholder-white/40
    p-2 transition-all duration-300
  "
              />

            </div>

            <button
              className="
    bg-amber-500 text-white font-semibold
    px-5 py-3 rounded-xl
    shadow-lg shadow-black/20
    hover:shadow-xl hover:shadow-black/30
    hover:bg-amber-600
    active:scale-95
    transition-all duration-300
  "
            >
              Login
            </button>

            <p className="text-white/60 text-sm text-center font-bold">
              Novo por aqui?{" "}
              <Link
                href="/cadastro"
                className="text-white hover:text-amber-400 underline-offset-4 hover:underline transition"
              >
                Cadastre-se
              </Link>
            </p>


          </form>

         
        </div>
      </div>
    </div>
  );
}
